main.main-container {
    width: 1200px;
    margin: 0 auto;
    border: 1px solid;
    /* height: 100vh; */
    padding: 15px;
}
.drag-and-drop-section {
    margin: 0 auto;
    width: 400px;
}
.drag-and-drop-section p {
    height: 100px;
    width: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border: 1px dashed #ccc;
    border-radius: 10px;
}
.thumb {
    display: 'inline-flex';
    border-radius: 2;
    border: '1px solid #eaeaea';
    margin-bottom: 8;
    margin-right: 8;
    width: '100%';
    box-sizing: 'border-box';
    text-align: center;
}
.text-center {
    text-align: center;
}
.text-danger {
    color: #ff01cc;
}
.crop-navigation {
    margin: 20px 72px;
}
.crop-navigation-step {
    padding: 10px;
    box-shadow: 1px 6px 17px #ccc;
    border-top: 2px solid;
}
.preview-wrapper {
    border-top: 2px solid #ccc;
    padding-top: 10px;
    margin-top: 10px;
}
.preview-wrapper h3 {
    display: inline-block;
    margin: 0;
}
.preview-wrapper .upload-section {
    float: right;
}
.preview-wrapper .preview-controls {
    padding-bottom: 20px;
}
.preview-section {
    display: flex;
    overflow: auto;
    align-items: center;
    padding: 15px;
    border: 3px solid #ccc;
    width: 100%;
}
.preview-section img {
    max-width: 100%;
    margin: 0 20px;
    box-shadow: 2px 2px 10px #000;
}
.upload-btn {
    background: #248b24;
    color: #fff;
    padding: 9px;
    cursor: pointer;
}
.form-control {
    padding: 5px;
    font-size: 16px;
}
.download-urls {
    padding: 0;
    list-style: none;
    width: 500px;
    margin: 0 auto;
}
.loading {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.8);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: #fff;
    font-weight: bold;
}